*{
  margin: 0;
  padding: 0;
  outline: none;
  box-sizing: border-box;
}

body{
  background-color: #111;
}


/* mobile portrait */

.title{
  padding: 1.5vh 0;
  background-color: #3F5;
  color: #111;
  text-align: center;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  font-size: 1.5rem;
  font-weight: 700;
}

.search-bar{
  width: 95vw;
  margin: 3vh 2.5vw;
  padding: 2.5% 4%;
  background-color: #111;
  color: #3F5;
  border: 1px solid #FFF9;
  border-radius: 5px;
}
.search-bar:hover{
  border: 1px solid #FFFC;
}
.search-bar::placeholder{
  color: #FFF9;
}
.search-bar:focus{
  border: 1px solid #3F5;
}


/* small mobile lanscape */

@media (min-width: 548px) and (orientation: landscape)
{
  .title{
    text-align: left;
    padding: 2vh 0;
    padding-left: 7vw;
  }
}


/* large mobile lanscape */

@media (min-width: 768px) and (orientation: landscape)
{
  .title{
    font-size: 2rem;
  }
}


/* tablet portrait */

@media (min-width: 768px) and (orientation: portrait)
{
  .title{
    font-size: 2.5rem;
  }
}


/* tablet landscape & laptop */

@media (min-width: 1440px)
{
  .title{
    font-size: 3rem;
  }
}


/* larger screen */

@media (min-width: 2560px)
{
  .title{
    font-size: 3.6rem;
  }
}